.newsCarouselWrapper {
    position: relative;
    width: 100%;
    height: 50vh;
    overflow: hidden; /* Обрезаем все, что выходит за границы */
}

.newsCarouselImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.newsCarouselGradientBottom {
    background: linear-gradient(
        to top,
        rgba(0, 16, 45, 1) 0%,
        rgba(0, 16, 45, 0.2) 45%,
        rgba(0, 16, 45, 0) 100%
    );
}
